import "./Login.css";
import React from "react";
import { Form, Button, Col } from "react-bootstrap";
import client from "./CLASSES/client";
import validation from "./Functions/validation";

class Login extends React.Component {
    username = "";
    passowrd = "";
    server = 1;
    state = { error: false }
    error = { username: "", password: "" }

    componentDidMount() {

    }

    login(event) {
        event.preventDefault();
        let text = validation.safeText(this.username, 3, 15);
        if (!text.valid) {
            this.error.username = text.result;
            this.setState({ error: true });
        } else {
            client.login(this.username, this.passowrd, this.server)
                .then(response => {
                    if (response.authorized) {
                        alert("OK");
                    } else {
                        this.error.username = response.e_username;
                        this.error.password = response.e_password;
                        this.setState({ error: true });
                    }
                });
        }
        return false;
    }

    render() {
        return <div className="game-login">
            <h1>الدخول إلى اللعبة</h1>
            <div className="game-login-top">
                <div><span>الدخول</span></div>
                <p>
                    عليك تفعيل ملفات التعريف (كوكيز) لتتمكن من تسجيل الدخول. و إذا تعدد مستخدموا هذا الحاسب يجب أن تضغط على تسجيل الخروج في كل مرة تنتهي من اللعب وذلك حفاظاً على بياناتك.
                </p>
            </div>
            <form onSubmit={event => this.login(event)}>
                <Form.Group className="game-login-form">
                    <Form.Row>
                        <Form.Label column sm={2}>اسم المستخدم</Form.Label>
                        <Col>
                            <Form.Control type="text" name="name" tabIndex={1} size="sm" onChange={e => (this.username = e.target.value)} placeholder="اسم المستخدم" />
                            <div className="error ta-right">{this.error.username}</div>
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Form.Label column sm={2}>كلمة المرور</Form.Label>
                        <Col>
                            <Form.Control type="password" name="password" tabIndex={2} size="sm" onChange={e => (this.passowrd = e.target.value)} placeholder="كلمة المرور" />
                            <div className="error ta-right">{this.error.password}</div>
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Form.Label column sm={2}>السيرفر<br /></Form.Label>
                        <Col className="servers-col">
                            <ServersList onChange={server => (this.server = server)} />
                        </Col>
                    </Form.Row>
                </Form.Group>
                <Button variant="secondary" type="submit">تسجيل الدخول</Button>
            </form>
        </div>
    }
}

class ServersList extends React.Component {
    state = { ready: false }
    /** @type {{ serverNumber: number, ready: boolean, ended: boolean, isFull: boolean, started: number, players: number }[]} */
    servers = null;

    componentDidMount() {
        client.listServers().then(response => {
            this.servers = response;
            this.setState({ ready: true });
        });
    }

    render() {
        if (this.state.ready && Array.isArray(this.servers)) {
            return <select className="servers-list" onChange={e => this.props.onChange(Number(e.target.value))}>
                {this.servers.map(server => <option key={"Server" + server.serverNumber} value={server.serverNumber}>السيرفر {server.serverNumber}</option>)}
            </select>;
        }
        else
            return <></>;
    }
}

export default Login;